  -webkit-animation-duration: 818ms;
}
.temp-modal-window .confetti .confetti-piece:nth-child(odd) {
  background: #7431e8;
}
.temp-modal-window .confetti .confetti-piece:nth-child(even) {
  z-index: 1;
}
.temp-modal-window .confetti .confetti-piece:nth-child(4n) {
  width: 5px;
  height: 12px;
  -webkit-animation-duration: 2000ms;
}
.temp-modal-window .confetti .confetti-piece:nth-child(3n) {
  width: 3px;
  height: 10px;
  -webkit-animation-duration: 2500ms;
  -webkit-animation-delay: 1000ms;
}
.temp-modal-window .confetti .confetti-piece:nth-child(4n-7) {
  background: red;
}
@-webkit-keyframes makeItRain {
  from {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  to {
    -webkit-transform: translateY(350px);
  }
}
.temp-modal-window .temp-modal-window__img {
  padding: 20px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: minmax(400px);
  gap: 10px;
}
.temp-modal-window .temp-modal-window__img .cats-gif {
  width: 400px;
  grid-column-start: 3;
  grid-row-start: 1;
}
.temp-modal-window .temp-modal-window__img .p-1 {
  grid-row-start: 2;
  grid-column-start: 1;
}
.temp-modal-window .temp-modal-window__img .p-2 {
  grid-row-start: 2;
  grid-column-start: 3;
}
.temp-modal-window .temp-modal-window__img .p-3 {
  grid-row-start: 3;
  font-size: 40px;
}
.temp-modal-window .temp-modal-window__img .happy-img {
  width: 400px;
  z-index: 1000;
  grid-row-start: 1;
  grid-row-end: 3;
  grid-column-start: 2;
  grid-column-end: 3;
  justify-self: center;
}
.temp-modal-window .temp-modal-window__img .totoro-first {
  grid-column-start: 1;
  grid-row-start: 3;
  justify-self: center;
}
.temp-modal-window .temp-modal-window__img .totoro-second {
  grid-column-start: 3;
  grid-row-start: 3;
  justify-self: center;
}
.temp-modal-window .temp-modal-window__img p {
  color: #fff;
  font-size: 70px;
  text-align: center;
}

header {
  display: flex;
  flex-direction: column;
  align-items: center;
}
header .header__main-page-link {
  font-size: 2rem;
  padding: 20px 6px;
}
header .menu {
  display: flex;
  flex-direction: row;
  list-style: none;
  gap: 10px;
  flex-wrap: wrap;
  padding-bottom: 6px;
}
header .menu .menu__link {
  padding: 4px 20px;
  font-size: 1.2rem;
  display: inline-block;
}
header .menu .menu__link:hover,
header .menu .menu__link:focus {
  color: #408f75;
  transition: all 0.25s ease-in-out;
}
header .menu .menu__link::after {
  display: block;
  content: "";
  padding-top: 4px;
  border-bottom: 2px solid #408f75;
  transition: transform 0.25s ease-in-out;
  transform: scaleX(0);
}
header .menu .menu__link:hover::after,
header .menu .menu__link:focus::after {
  transform: scaleX(1);
}

main .photo-container {
  padding: 14px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 14px;
}
main .photo-container .photo-container__item img {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}

footer {
  position: fixed;
  bottom: 0;
  right: 0;
  display: flex;
  justify-content: end;
  padding: 10px;
}
footer .links {
  display: inline-block;
  padding: 6px;
}
footer .links .links__icon {
  padding: 0 4px;
}
footer .links .links__icon img {
  height: 32px;
}